<template>
    <v-container class="d-flex flex-column my-5 mx-auto big-box">
        <v-row>
            <v-col class="box-1">
                <h1 class="text-center my-5">¡BUSCA A TU POKEMON!</h1>
                <v-row>
                    <v-col cols="6" class="mx-auto my-3">
                        <v-text-field v-model="pokemon_id" label="Ingresa el nombre en minúsculas" clearable>
                        </v-text-field>
                    </v-col>
                </v-row>
                <v-row >
                    <v-btn color="error" dark large class="mx-auto" @click="getPokemon">
                        BUSCAR
                    </v-btn>
                </v-row>
            </v-col>

            <v-col class="align-self-center">
                <v-img src="https://cdn.streamloots.com/uploads/5eb3db772a3fcd0035f7ff40/10172dc2-f05e-4804-948f-94ec8a1747ce.gif" width="200" class="mx-auto my-auto" v-show="pokemon.name == ''"></v-img>
                <v-card class="mx-auto" max-width="344" elevation="24" shaped v-show="pokemon.name != ''">
                    <v-img :src="imagen" height="250" class="my-2"></v-img>

                    <v-card-title class="green lighten-5">
                       <span class="font-weight-black"> {{ nombre }}</span>
                    </v-card-title>

                    <v-card-subtitle class="green lighten-5">
                       <span class="font-weight-black"> ID: {{ pokemon.id }} </span>
                    </v-card-subtitle>
                    <v-card-actions class="light-blue lighten-4">
                        <v-btn color="light-blue darken-4" text>
                            INFORMACIÓN
                        </v-btn>

                        <v-spacer></v-spacer>

                        <v-btn icon @click="show = !show">
                            <v-icon>{{ show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
                        </v-btn>
                    </v-card-actions>

                    <v-expand-transition>
                        <div v-show="show">
                            <v-divider></v-divider>

                            <v-card-text>
                                <span class="font-weight-black"> MOVIMIENTOS:</span>
                                <ul>
                                    <li v-for="move in movimiento" :key="move">{{ move }}</li>
                                </ul>
                               <span class="font-weight-black"> HABILIDADES:</span>
                                <ul>
                                    <li v-for="habilidad in habilidades" :key="habilidad">{{ habilidad }}</li>
                                </ul>
                            </v-card-text>
                        </div>
                    </v-expand-transition>
                </v-card>
            </v-col>
        </v-row>
        <v-row class="mx-auto">
            <div class="text-center">
                <v-dialog v-model="dialog" width="500">
                    <template v-slot:activator="{ on, attrs }">
                        <v-btn color="red lighten-2" dark class="mx-2" fab v-bind="attrs" v-on="on">
                            <v-icon light>
                                mdi-chat-question
                            </v-icon>
                        </v-btn>
                    </template>

                    <v-card>
                        <v-card-title class="text-h5 grey lighten-2">
                            ¿Cómo funciona?
                        </v-card-title>

                        <v-card-text class="my-1">
                            Random Pokedex te permite buscar los detalles más importantes de un pokemón, ingresando solo
                            un
                            nombre para la búsqueda. Conocerás tanto su ID, como movimientos y habilidades.
                        </v-card-text>

                        <v-divider></v-divider>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="primary" text @click="dialog = false">
                                ¡Entendido!
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </div>
        </v-row>
    </v-container>
</template>

<script>
export default {
    name: 'search-view',
    // props: {},
    data: function () {
        return {
            pokemon_id: '',
            pokemon: {
                id: '',
                name: '',
                abilities: [],
                sprites: {
                    font_default: ''
                },
                moves: []
            },
            pokemons: [],
            dialog: false,
            show: false
        }
    },
    computed: {
        habilidades() {
            return this.pokemon.abilities.map((habilidad) => {
                return habilidad.ability.name
            })
        },
        imagen() {
            return this.pokemon.sprites.front_default
        },

        movimiento() {
            return this.pokemon.moves.map((move) => {
                return move.move.name
            })
        },
        nombre() {
            let nombre = this.pokemon.name
            return nombre.toUpperCase()
        }

    },
    methods: {
        async getPokemon() {
            try {
                let response = await fetch(`https://pokeapi.co/api/v2/pokemon/${this.pokemon_id}`)
                let data = await response.json()
                this.setPokemon(data)
    
            } catch (error) {
                console.log(error)
            }
        },
        setPokemon(pokemon) {
            this.pokemon.id = pokemon.id
            this.pokemon.name = pokemon.name
            this.pokemon.abilities = pokemon.abilities
            this.pokemon.sprites = pokemon.sprites
            this.pokemon.moves = pokemon.moves

            let myPokemons = JSON.parse(JSON.stringify(this.pokemon))
            this.pokemons.push(myPokemons)
        }
    }
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    // -- End Lifecycle Methods
}
</script>

<style scoped>
.big-box {
    height: 95vh;
}
.box-1{
    margin-top: 3.125rem;
}

h1{
    font-size: 2.5rem;
}
</style>